import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-addupdate-item',
  templateUrl: './addupdate-item.component.html',
  styleUrls: ['./addupdate-item.component.css']
})
export class AddupdateItemComponent implements OnInit {

	public object:any = {
        name:'',
        address:'',
        city:'',
        postal_code:null,
        email:'',
        mobile:null,
        website:'',
        about_us:'',
        image:'',
        logo:'',
        lat:1,
        businessType:'doctor',
        medical:null,
        recreation:null,
        lng:1,
        isFeatured:false,
        scheduler: []
    };
    public itemID = ''
    public roles:any;
    public  days = ['Monday','Tuesday','Wednesday','Thursday','Friday','Saturday','Sunday']
    public showMyMenu = true;
  
    constructor(
      private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      this._sharedService.get('getDispensary').then((res: any) => {
          if(res.success) {
              this.object = res.data.key; 
              this.itemID = this.object.id;
          } else {

            this.days.forEach((key)=>{
                let obj = {
                    day:key,
                    startTime:'9:00AM',
                    closeTime:'5:00PM'
                }
                this.object.scheduler.push(obj)
                
            })
            this.showMyMenu = false;

          }
      },(error) => { 
         this.days.forEach((key)=>{
             let obj = {
                  day:key,
                  startTime:'9:00AM',
                  closeTime:'5:00PM'
              }
              this.object.scheduler.push(obj)
          })
          this.showMyMenu = false;
      });
    }

    save() {
      let data = JSON.parse(JSON.stringify(this.object));
      this._sharedService.loader('show');
       if(this.itemID) {
         let path = 'edit_dispensary/' + this.itemID;
           this._sharedService.put(data,path).then((res: any) => {
               this._sharedService.loader('hide');
              if(res.success) {
                  
              } else {
              }
          },(error) => { });
       } else {
         this._sharedService.post(data,'add_dispensary').then((res: any) => {
             this._sharedService.loader('hide');
            if(res.success) {
                  this.showMyMenu = true;      
            } else {
            }
        },(error) => { });
       }
    }

}



