import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-products-listing',
  templateUrl: './products-listing.component.html',
  styleUrls: ['./products-listing.component.css']
})
export class ProductsListingComponent implements OnInit {

	public products = [];
	public page = 1;
	public count = 100;
	public category_id = '';
	public categories = []
  public roles:any;
  constructor(
      private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.getAllCategories();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      this._sharedService.loader('show');
      this._sharedService.AllProductItemByCategory(this.category_id,this.page,this.count).then((res: any) => {
        this._sharedService.loader('hide');
          if(res.success) {
              this.products = res.data.data ? res.data.data : [];
          } else {
          }
      },(error) => { });
    }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
            } else {
            }
        },err => {  });
    }
}



