import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-my-menu',
  templateUrl: './my-menu.component.html',
  styleUrls: ['./my-menu.component.css']
})
export class MyMenuComponent implements OnInit {

	products   = []
    categories = []
    dispenaryID:any
    public roles:any;
  constructor(
      private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      this._sharedService.loader('show');
      this._sharedService.get('AllProductByUser').then((res: any) => {
        this._sharedService.loader('hide');
          if(res.success) {
              this.products = res.data.key ? res.data.key : []; 
              this.getAllCategories()
          } else {
          }
      },(error) => { this._sharedService.loader('hide'); });
      this._sharedService.get('getDispensary').then((res: any) => {
          if(res.success) {
              let object = res.data.key; 
              this.dispenaryID = object.id;
          } else {
          }
      },(error) => { });
    }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
                if(this.products.length==0) this.addObject();
            } else {
            }
        },err => {  });
    }

    save() {
        let arrayObj              = JSON.parse(JSON.stringify(this.products));
        arrayObj.forEach((obj)=>{
            obj.dispensary_id = this.dispenaryID;
        })
        let data = {data:arrayObj};
        this._sharedService.loader('show');
        this._sharedService.post(data,'addUserItemProduct').then(res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.categories = res.data.category;
                if(this.products.length==0) this.addObject();
            } else {
            }
        },err => {  });
    }

    addObject() {
        let obj = {
          "name": "",
          "pre_roll": "",
          "Eighth": "",
          "quarter": "",
          "half": "",
          "ounce": "",
          "category_id": this.categories[0]['id'],
          "details": "",
          "thc": "",
          "image": "",
          "isDeleted": false,
          "cbd": "",
          "status": "active",
          "brand_name": "",
          "product_id": ""
        }
        this.products.push(obj);
    }

}



