import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddupdateItemComponent } from './addupdate-item/addupdate-item.component';
import { MyMenuComponent } from './my-menu/my-menu.component';
import { ReviewsComponent } from './reviews/reviews.component';
import { AccountInfoComponent } from './account-info/account-info.component';
import { ProductsListingComponent } from './products-listing/products-listing.component';

const routes: Routes = [{
		path: '',
		component: AddupdateItemComponent
	},{
		path: 'my-menu',
		component: ProductsListingComponent
	},{
		path: 'addupdate-products',
		component: MyMenuComponent
	},{
		path: 'reviews',
		component: ReviewsComponent
	},{
		path: 'account-info',
		component: AccountInfoComponent
	}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ItemDashboardRoutingModule { }



