 import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.component.html',
  styleUrls: ['./reviews.component.css']
})
export class ReviewsComponent implements OnInit {


    public arrayObj = []
    selectedIndex:any;
    reply:any
    count
    average
    roles:any;
    
    constructor(
      private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      this._sharedService.get('reviews').then((res: any) => {
          if(res.success) {
              this.arrayObj = res.data.reviews; 
              this.count = res.data.count;
              this.average = res.data.avg ? res.data.avg : 0;
          } else {
          }
      },(error) => { });
    }

    send(index,id) {
    	let data = {
    		review_id:id,
			detail:this.reply,
    		
    	}
      this._sharedService.loader('show');
    	this._sharedService.post(data,'replies').then((res: any) => {
          this._sharedService.loader('hide');
          if(res.success) {
              this.reply = '';
              this.selectedIndex = null;
          } else {
          }
      },(error) => { });
    }
}



