import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-account-info',
  templateUrl: './account-info.component.html',
  styleUrls: ['./account-info.component.css']
})
export class AccountInfoComponent implements OnInit {

    public _host      = environment.config.BASE_URL;
	public user:any   = {};
	public obj = {
		currentPassword:'',
		newPassword:'',
		confirmPassword:''
	}
	public errMessage = '';
	public successMessage = '';
    public roles:any;

    constructor(
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
         this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
        this._sharedService.get('getuserdetail').then((res: any) => {
            if(res.success) {
                this.user = res.data.user; 
            } else {
            }
        },(error) => { });
    }

    changePassword() {
    	this.errMessage = '';
        this.successMessage = '';

    	let body = {
    		currentPassword:this.obj.currentPassword,
    		newPassword:this.obj.newPassword,
    		confirmPassword:this.obj.confirmPassword
    	}
        this._sharedService.loader('show');
        this._sharedService.put(body,'changepassword').then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.successMessage = res.message;
                this.obj = {
                    currentPassword:'',
                    newPassword:'',
                    confirmPassword:''
                }
            } else {
                this.errMessage = res.error.message;
            }
        },(error) => { });
    }

}



