import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgxSpinnerModule } from 'ngx-spinner';


import { ItemDashboardRoutingModule } from './item-dashboard-routing.module';
import { AddupdateItemComponent } from './addupdate-item/addupdate-item.component';
import { ReviewsComponent } from './reviews/reviews.component';
import { AccountInfoComponent } from './account-info/account-info.component';
import { MyMenuComponent } from './my-menu/my-menu.component';
import { ProductsListingComponent } from './products-listing/products-listing.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    NgxSpinnerModule,
    ItemDashboardRoutingModule
  ],
  declarations: [AddupdateItemComponent, ReviewsComponent, AccountInfoComponent, MyMenuComponent, ProductsListingComponent]
})
export class ItemDashboardModule { }



